var webRTCView = Backbone.View.extend({
    variables: function(){
        this.selectOutput = this.$el.find("#output");
    },
    initialize: function(options) {
        this.variables();
        this.userSettings = options.userSettings;
        var output = this.userSettings["output"];
        this.initWebRTC(output);
        this.bindListeners();
    },
    bindListeners: function(){
        this.selectOutput.on('change', function() {
            var saveSettings = {target: "output", options: $(this).val()};
            $(window).trigger("saveSettings", saveSettings);
        });
    },
    initWebRTC: function(output) {
        function gotDevices(deviceInfos) {
            var masterOutputSelector = document.getElementById('output');
            masterOutputSelector.addEventListener('change', changeAudioDestination);

            for (var i = 0; i !== deviceInfos.length; ++i) {
                var deviceInfo = deviceInfos[i];
                var option = document.createElement('option');
                option.value = deviceInfo.deviceId;
                if (deviceInfo.kind === 'audiooutput') {
                    console.info('Found audio output device: ', deviceInfo.label);
                    option.text = deviceInfo.label || 'speaker ' +
                        (masterOutputSelector.length + 1);
                    masterOutputSelector.appendChild(option);
                } else {
                    console.log('Found non audio output device: ', deviceInfo.label);
                }
                if (option.value == output ) {
                    option.setAttribute("selected", "");
                    var element = document.getElementById('audio-stream');
                    var deviceId = output;
                    var outputSelector = document.getElementById('output');
                    attachSinkId(element, deviceId, outputSelector);
                }
            }
        }

        navigator.mediaDevices.enumerateDevices().then(gotDevices).catch(handleError);

        // Attach audio output device to the provided media element using the deviceId.
        function attachSinkId(element, sinkId, outputSelector) {
            if (typeof element.sinkId !== 'undefined') {
                element.setSinkId(sinkId)
                    .then(function() {
                        console.log('Success, audio output device attached: ' + sinkId + ' to ' +
                            'element with ' + element.title + ' as source.');
                    })
                    .catch(function(error) {
                        var errorMessage = error;
                        if (error.name === 'SecurityError') {
                            errorMessage = 'You need to use HTTPS for selecting audio output ' +
                                'device: ' + error;
                        }
                        console.error(errorMessage);
                        // Jump back to first output device in the list as it's the default.
                        outputSelector.selectedIndex = 0;
                    });
            } else {
                console.warn('Browser does not support output device selection.');
            }
        }

        function changeAudioDestination(event) {
            var deviceId = event.target.value;
            var outputSelector = event.target;
            var element = document.getElementById('audio-stream');
            attachSinkId(element, deviceId, outputSelector);
        }

        function gotStream(stream) {
            window.stream = stream; // make stream available to console
        }

        function start() {
            if (window.stream) {
                window.stream.getTracks().forEach(function(track) {
                    track.stop();
                });
            }
            var constraints = {
                audio: true,
                video: false
            };
            navigator.mediaDevices.getUserMedia(constraints).then(gotStream).catch(handleError);
        }

        start();

        function handleError(error) {
            console.log('Error: ', error);
        }
    }
});
module.exports = webRTCView;



