var ResponsiveVoiceRTCView = Backbone.View.extend({
    variables: function() {
        this.selectVoices = this.$el.find("#voices");
        this.selectRate = this.$el.find("#rate");
        this.selectPitch = this.$el.find("#pitch");
        this.selectVolume = this.$el.find("#volume");

        this.audioStream = $("#audio-stream");

        this.json = require('./api.json');
    },
    initSettings: function() {
        var scope = this;
        this.$el.off('*');
        this.$el.empty();
        //user settings and defaults
        this.parameters = {
            voice: {
                name: "UK English Female",
                lang: "en-GB",
                sv: "",
                vn: ""
            },
            rate: 1,
            pitch: 1.1,
            volume: 1
        }
        var template = require('./../../../../../templates/setting.html');
        var settings = {
            $el: this,
            select: [{
                name: "Voices",
                id: "voices"
            }],
            range: [{
                    name: "Rate",
                    id: "rate",
                    min: "0",
                    max: "2",
                    value: scope.parameters.rate,
                    step: "0.1"
                },
                {
                    name: "Pitch",
                    id: "pitch",
                    min: "0",
                    max: "2",
                    value: scope.parameters.pitch,
                    step: "0.1"
                },
                {
                    name: "Volume",
                    id: "volume",
                    min: "0",
                    max: "1",
                    value: scope.parameters.rate,
                    step: "0.1"
                }
            ]
        }
        var settingHTML = template({
            setting: settings
        });
        this.$el.append(settingHTML);
        this.variables();
        voiceList = this.json.voices;
        voiceList = _.sortBy(voiceList, 'name');
        $.each(voiceList, function(key, value) {
            if (value.name == scope.parameters.voice.name) {
                scope.selectVoices.append('<option value="' + value.name + '" selected>' + value.name + '</option>');
            } else {
                scope.selectVoices.append('<option value="' + value.name + '">' + value.name + '</option>');
            }
        });
    },
    initialize: function() {
        this.listener = {};
        _.extend(this.listener, Backbone.Events);
        console.log("Responsive Voice RTC Api");
        this.initSettings();
        this.bindListeners();
    },
    destroy: function(){
        this.audioStream.off('*');
    },
    bindListeners: function() {
        var scope = this;
        this.selectVoices.on('change', function() {
            var voiceName= $(this).val();
            scope.parameters.voice.name = voiceName;
            for (var i = 0; i < scope.json.voices.length; i++) {
                if (scope.json.voices[i].name == voiceName) {
                    scope.parameters.voice.lang = scope.json.voices[i].flag;
                    scope.parameters.voice.sv = scope.json.voices[i].sv;
                    scope.parameters.voice.vn = scope.json.voices[i].vn;
                }
            }
            console.log("New voice: ", voiceName);
            var saveSettings = {target: "apis.responsiveVoiceRTC.voice", options: $(this).val()};
            $(window).trigger("saveSettings", saveSettings);
        });
        this.selectRate.on('change', function() {
            scope.parameters.rate = $(this).val();
            console.log("New rate: ", scope.parameters.rate);
            var saveSettings = {target: "apis.responsiveVoiceRTC.rate", options: $(this).val()};
            $(window).trigger("saveSettings", saveSettings);
        });
        this.selectPitch.on('change', function() {
            scope.parameters.pitch = $(this).val();
            console.log("New pitch :", scope.parameters.pitch);
            var saveSettings = {target: "apis.responsiveVoiceRTC.pitch", options: $(this).val()};
            $(window).trigger("saveSettings", saveSettings);
        });
        this.selectVolume.on('change', function() {
            scope.parameters.volume = $(this).val();
            console.log("New volume: ", scope.parameters.volume);
            var saveSettings = {target: "apis.responsiveVoiceRTC.volume", options: $(this).val()};
            $(window).trigger("saveSettings", saveSettings);
        });
        this.audioStream.on('ended', function() {
            scope.listener.trigger("messageEnd");
        });
        this.audioStream.on('error', function() {
            scope.listener.trigger("messageEnd");
        });
    },
    render: function() {

    },
    speak: function(message) {
        var message = encodeURIComponent(message);
        var tl = this.parameters.voice.lang;
        var sv = this.parameters.voice.sv;
        var vn = this.parameters.voice.vn;
        var pitch = this.parameters.pitch/2;
        var rate = this.parameters.rate/2;
        var volume = this.parameters.volume;
        var uri = "https://code.responsivevoice.org/getvoice.php?t="+message+"&tl="+tl+"&sv="+sv+"&vn="+vn+"&pitch="+pitch+"&rate="+rate+"&vol="+volume
        this.audioStream.attr('src', uri);
        this.audioStream[0].load();
        this.listener.trigger("messageStart");
        this.audioStream[0].play();
    },
    show: function() {

    },
    hide: function() {

    },
});
module.exports = ResponsiveVoiceRTCView;



