var ClarenceView = Backbone.View.extend({
    variables: function() {
        // App requirements
        this.writing = this.$el.find("#writing .main-input");
        this.hasMessage = true;

        // Message requirements
        this.message = "";
    },
    initialize: function() {
        this.variables();
        this.render();
    },
    render: function() {
        this.bindListeners();
    },
    bindListeners: function() {
        var scope = this;
        // Window events
        $(window).on('globalOn', function(){
            scope.hasMessage = true;
            scope.writing.focus();
        });
        $(window).on('globalOff', function(){
            scope.hasMessage = true;
        });

        // Inputs on App blur state
        $(window).on('insertLetter', function(event, letter){
            scope.emptyText(scope.hasMessage);
            scope.message += letter
            scope.writeLetter(letter);
            console.log(scope.message);
        })
        $(window).on('spaceLetter', function(event, letter){
            scope.emptyText(scope.hasMessage);
            // if(scope.mode == "sentence"){
                scope.message += letter
                scope.writeLetter(" ");
                console.log(scope.message);
            // } else if(scope.mode == "word"){
                // $(window).trigger('validateMessage')
            // }
        })
        $(window).on('deleteLetter', function(event, letter){
            scope.emptyText(scope.hasMessage);
            scope.message = scope.message.slice(0, -1);
            scope.deleteLetter();
            console.log(scope.message);
        })
        $(window).on('validateMessage', function(event, letter){
            scope.hasMessage = true;
        })

        // Misc
        $(window).on('clearMessage', function(){
            scope.message = "";
            console.log("message cleared");
        });
    },
    emptyText: function(hasMessage){
        if (hasMessage) {
            this.hasMessage = false;
            this.message = "";
            this.writing.val("");
        }
    },
    writeLetter: function(letter){
        // console.log(letter);
        var phrase = this.writing.val();
        this.writing.val(phrase + letter);
        this.writing.focus();
        this.writing[0].scrollLeft = this.writing[0].scrollWidth;
    },
    deleteLetter: function(){
        var phrase = this.writing.val();
        this.writing.val(phrase.slice(0, -1));
        this.writing.focus();
        this.writing[0].scrollLeft = this.writing[0].scrollWidth;
    },
    show: function() {

    },
    hide: function() {

    }
});
module.exports = ClarenceView;



