var webRTC = require('./webRTC_view');
var PollyRTCApi = require('./../apis/polly/pollyrtc_view');
var ResponsiveVoiceRTCApi = require('./../apis/responsivevoice/responsivevoicertc_view');

var ClarenceView = Backbone.View.extend({
    variables: function() {
        var scope = this;

        // App requirements
        this.content = this.$el.find("#content");
        this.selectMode = this.$el.find("#mode");
        this.selectKeyboard = this.$el.find("#keyboard");
        this.selectApi = this.$el.find("#api");
        this.mode = this.selectMode.val();
        this.settingsToggle = this.$el.find("#settingsToggle");
        this.settings = this.$el.find("#settings");
        this.apiSettings = this.$el.find("#api_settings");
        this.menuIsOpen = false;
        this.globalAccessOnly = false;
        this.writing = this.$el.find("#writing .main-input");
        this.hasMessage = true;

        // Message requirements
        this.message = "";
        this.isPlaying = false;
        this.buffered = [];
    },
    initialize: function() {
        var scope = this;
        this.variables();

        // User settings
        $.getJSON(this.settings.attr("data-json"), function(data){
            scope.userSettings = data;
            scope.render();
        });
    },
    render: function() {
        var scope = this;
        switch(scope.userSettings.api){
            case "responsivevoiceRTC":
                scope.api = new ResponsiveVoiceRTCApi({el: scope.apiSettings, userSettings: scope.userSettings});
                scope.selectApi.val("responsivevoiceRTC");
                break;
            case "pollyRTC":
                scope.api = new PollyRTCApi({el: scope.apiSettings, userSettings: scope.userSettings});
                scope.selectApi.val("pollyRTC");
                break;
            default:
                scope.api = new PollyRTCApi({el: scope.apiSettings, userSettings: scope.userSettings});
                scope.selectApi.val("pollyRTC");
        }
        // this.api = new ResponsiveVoiceRTCApi({el: this.apiSettings, userSettings: this.userSettings});
        // this.api = new PollyRTCApi({el: this.apiSettings, userSettings: this.userSettings});
        this.webRTC = new webRTC({el: this.settings, userSettings: this.userSettings});
        this.api.listener.bind("messageStart",  scope.messageStart, scope);
        this.api.listener.bind("messageEnd",  scope.messageEnd, scope);
        this.api.listener.bind("messageClear",  scope.messageClear, scope);
        this.bindListeners();
        this.writing.focus();
    },
    bindListeners: function() {
        var scope = this;
        // Window events
        $(window).on('focus', function(){
            scope.globalAccessOnly = false;
            if (!scope.menuIsOpen) {
                scope.$el.addClass("focused");
                scope.writing.focus();
            }
        });
        $(window).on('blur', function(){
            scope.globalAccessOnly = true;
            scope.$el.removeClass("focused");
        });
        $(window).on('globalOn', function(){
            scope.hasMessage = true;
            if (scope.settings.hasClass("opened")) {
                scope.menuIsOpen = false;
                scope.settings.removeClass("opened");
            }
            scope.$el.addClass("focused");
            scope.writing.focus();
        });
        $(window).on('globalOff', function(){
            scope.hasMessage = true;
            if (scope.globalAccessOnly) {
                scope.$el.removeClass("focused");
            }
        });
        this.settingsToggle.on('click', function(){
            scope.settings .toggleClass("opened");
            if (scope.settings.hasClass("opened")) {
                scope.menuIsOpen = true;
                scope.$el.removeClass("focused");
            } else {
                scope.menuIsOpen = false;
                scope.$el.addClass("focused");
                scope.writing.focus();
            }
        });
        this.content.on('click', function(){
            scope.settings .removeClass("opened");
            scope.menuIsOpen = false;
            scope.$el.addClass("focused");
            scope.writing.focus();
        });

        // Inputs on App focus state
        $(window).on('keypress', function(e){
            if (!scope.menuIsOpen) {
                scope.emptyText(scope.hasMessage);
                if(scope.mode == "sentence"){
                    scope.sentenceBySentence(e);
                } else if(scope.mode == "word"){
                    scope.wordByWord(e);
                }
                // e.preventDefault();
                scope.resizeTextarea();
            }
        });
        $(window).on('keydown', function(e){
            var keyCode = e.keyCode
            if(keyCode == 8) { //Backspace
                scope.emptyText(scope.hasMessage);
                scope.message = scope.message.slice(0, -1);
                scope.deleteLetter();
                console.log(keyCode, String.fromCharCode(keyCode).toLowerCase(), scope.message);
            }
        });

        // Inputs on App blur state
        $(window).on('insertLetter', function(event, letter){
            scope.emptyText(scope.hasMessage);
            scope.message += letter
            scope.writing.val(scope.writing.val() + letter);
            scope.writing[0].scrollLeft = scope.writing[0].scrollWidth;
            console.log(scope.message);
        })
        $(window).on('spaceLetter', function(event, letter){
            scope.emptyText(scope.hasMessage);
            if(scope.mode == "sentence"){
                scope.message += letter
                scope.writing.val(scope.writing.val() + letter);
                scope.writing[0].scrollLeft = scope.writing[0].scrollWidth;
                console.log(scope.message);
            } else if(scope.mode == "word"){
                $(window).trigger('validateMessage');
            }
        })
        $(window).on('deleteLetter', function(event, letter){
            scope.emptyText(scope.hasMessage);
            scope.message = scope.message.slice(0, -1);
            scope.writing.val(scope.writing.val().slice(0, -1));
            scope.writing[0].scrollLeft = scope.writing[0].scrollWidth;
        })
        $(window).on('validateMessage', function(event, letter){
            scope.emptyText(scope.hasMessage);
            if (!scope.isPlaying) { // Play
                if (scope.message != "") {
                    scope.voiceInstance(scope.message)
                    scope.message = "";
                }
            } else { // Buffer
                if (scope.buffered.length >= 1) { // we fuse the texts || state[2]
                    var oldMessage = scope.buffered[0] + " ";
                    var mergedMessage = oldMessage + " " + scope.message;
                    scope.buffered = [];
                    scope.buffered.push(mergedMessage);
                    console.log("________NEW merge:");
                    console.log(mergedMessage);
                    scope.message = "";
                } else { // we just put it in the array || state[1]
                    scope.buffered.push(scope.message);
                    console.log("____NEW push:");
                    console.log(scope.message);
                    scope.message = "";
                }
            }
        })

        // Misc
        $(window).on('clearMessage', function(){
            scope.message = "";
            console.log("message cleared");
        });
        this.selectMode.val(scope.userSettings["mode"]);
        this.selectMode.on('change', function(){
            scope.mode = $(this).val();
            console.log(scope.mode);
            var saveSettings = {target: "mode", options: $(this).val()}
            $(window).trigger("saveSettings", saveSettings)
        });
        this.selectKeyboard.val(scope.userSettings["keyboard"]);
        this.selectKeyboard.on('change', function(){
            var saveSettings = {target: "keyboard", options: $(this).val()}
            $(window).trigger("saveSettings", saveSettings)
        });
        this.selectApi.on('change', function(){
            scope.api.destroy();
            scope.api.listener.off("messageStart");
            scope.api.listener.off("messageEnd");
            scope.api.listener.off("messageClear");
            switch($(this).val()){
                case "responsivevoiceRTC":
                    scope.api = new ResponsiveVoiceRTCApi({el: scope.apiSettings, userSettings: scope.userSettings});
                    scope.selectApi.val("responsivevoiceRTC");
                    break;
                case "pollyRTC":
                    scope.api = new PollyRTCApi({el: scope.apiSettings, userSettings: scope.userSettings});
                    scope.selectApi.val("pollyRTC");
                    break;
                default:
                    scope.api = new PollyRTCApi({el: scope.apiSettings, userSettings: scope.userSettings});
                    scope.selectApi.val("pollyRTC");
            }
            scope.api.listener.bind("messageStart",  scope.messageStart, scope);
            scope.api.listener.bind("messageEnd",  scope.messageEnd, scope);
            scope.api.listener.bind("messageClear",  scope.messageClear, scope);
            var saveSettings = {target: "api", options: $(this).val()};
            $(window).trigger("saveSettings", saveSettings);
            scope.emptyText();
        });
    },
    sentenceBySentence: function(e){
        var keyCode = e.keyCode
        if (keyCode == 13 && this.isPlaying == false && this.message != "") { // if it's not playing play the new word || state[0]
            this.voiceInstance(this.message);
            console.log("__NEW:");
            console.log(this.message)
            this.message = "";
            return false
        } else if (keyCode == 13 && this.isPlaying == true && this.message != "") { // else buffer the message
            if (this.buffered.length >= 1) { // we fuse the texts || state[2]
                var oldMessage = this.buffered[0] + " ";
                var mergedMessage = oldMessage + " " + this.message;
                this.buffered = [];
                this.buffered.push(mergedMessage);
                console.log("________NEW merge:");
                console.log(mergedMessage)
                this.message = "";
            } else { // we just put it in the array || state[1]
                this.buffered.push(this.message);
                console.log("____NEW push:");
                console.log(this.message)
                this.message = "";
            }
            return false
        } else {
            this.message += String.fromCharCode(keyCode).toLowerCase();
            this.writeLetter(String.fromCharCode(keyCode).toLowerCase());
            console.log(keyCode, String.fromCharCode(keyCode).toLowerCase(), this.message);
        }
    },
    wordByWord: function(e){
        var keyCode = e.keyCode
        if ((keyCode == 32 || keyCode == 13) && this.isPlaying == false && this.message != "") { // if it's not playing play the new word || state[0]
            this.voiceInstance(this.message);
            console.log("__NEW:");
            console.log(this.message);
            this.message = "";
            return false
        } else if ((keyCode == 32 || keyCode == 13) && this.isPlaying == true && this.message != "") { // else buffer the message
            if (this.buffered.length >= 1) { // we fuse the texts || state[2]
                var oldMessage = this.buffered[0] + " ";
                var mergedMessage = oldMessage + " " + this.message;
                this.buffered = [];
                this.buffered.push(mergedMessage);
                console.log("________NEW merge:");
                console.log(mergedMessage);
                this.message = "";
            } else { // we just put it in the array || state[1]
                this.buffered.push(this.message);
                console.log("____NEW push:");
                console.log(this.message);
                this.message = "";
            }
            return false
        } else {
            this.message += String.fromCharCode(keyCode).toLowerCase();
            this.writeLetter(String.fromCharCode(keyCode).toLowerCase());
            console.log(keyCode, String.fromCharCode(keyCode).toLowerCase(), this.message);
        }
    },
    voiceInstance: function(message){
        this.api.speak(message)
    },
    messageStart: function(){
        console.log("_messageStart");
        this.isPlaying = true;
        this.hasMessage = true;
    },
    messageEnd: function(){
        console.log("_messageEnd");
        this.isPlaying = false;
        if (this.buffered.length >= 1) { // Prepare the following || state[3]
            message = this.buffered[0];
            if (message != "") {
                this.buffered = [];
                this.voiceInstance(message);
                console.log("Playing buffered");
            } else {
                this.buffered = [];
            }
        }
    },
    messageClear: function(){
        console.log("messageClear");
        this.message = "";
    },
    emptyText: function(hasMessage){
        if (hasMessage) {
            console.log("emptyText()");
            this.hasMessage = false;
            this.message = "";
            this.writing.val("");
        }
    },
    writeLetter: function(letter){
        // var phrase = this.writing.val();
        // this.writing.val(phrase + letter);
    },
    deleteLetter: function(){
        // var phrase = this.writing.val();
        // this.writing.val(phrase.slice(0, -1));
        if (window.getSelection() != ""){
            selection = window.getSelection().toString().toLowerCase();
            newValue = this.writing.val().replace(selection, '');
            this.message = newValue;
        }
        console.log(this.message);
    },
    resizeTextarea: function(){
        // this.writing[0].style.height = "1px";
        // this.writing[0].style.height = (this.writing[0].scrollHeight)+"px";
    },
    show: function() {

    },
    hide: function() {

    }
});
module.exports = ClarenceView;



